import React, { useState, useEffect } from 'react';
import { useParams, Link } from 'react-router-dom';

const AuctionListing = () => {
  const { id } = useParams();
  const [auction, setAuction] = useState(null);
  const [bidAmount, setBidAmount] = useState('');

  useEffect(() => {
    // Simulating fetching auction details
    const fetchAuction = () => {
      setAuction({
        id,
        address: `${id}23 Main St, Anytown, USA`,
        seller: `0x1234...${id}678`,
        startingPrice: (100000 + id * 25000).toString(),
        endTime: new Date(Date.now() + 86400000).toISOString(),
      });
    };

    fetchAuction();
  }, [id]);

  const handleBid = (e) => {
    e.preventDefault();
    console.log(`Placing bid of ${bidAmount} USDC for property ${id}`);
    alert(`Bid of ${bidAmount} USDC placed successfully!`);
    setBidAmount('');
  };

  if (!auction) {
    return <div>Loading auction details...</div>;
  }

  return (
    <div>
      <h2>Auction for Property #{auction.id}</h2>
      <p>Address: {auction.address}</p>
      <p>Seller: {auction.seller}</p>
      <p>Starting Price: {auction.startingPrice} USDC</p>
      <p>Auction Ends: {new Date(auction.endTime).toLocaleString()}</p>
      
      <form onSubmit={handleBid}>
        <input
          type="number"
          value={bidAmount}
          onChange={(e) => setBidAmount(e.target.value)}
          placeholder="Enter bid amount in USDC"
          min={auction.startingPrice}
        />
        <button type="submit">Place Bid</button>
      </form>
      <Link to="/">Back to Property List</Link>
    </div>
  );
};

export default AuctionListing;