import React from 'react';
import { Link } from 'react-router-dom';

const properties = [
  { id: 1, address: "123 Main St, Anytown, USA", startingPrice: "100000" },
  { id: 2, address: "456 Elm St, Somewhere, USA", startingPrice: "150000" },
  { id: 3, address: "789 Oak St, Nowhere, USA", startingPrice: "200000" },
  { id: 4, address: "101 Pine St, Everywhere, USA", startingPrice: "175000" },
  { id: 5, address: "202 Maple St, Anywhere, USA", startingPrice: "225000" }
];

const PropertyList = () => {
  return (
    <div>
      <h2>Available Properties</h2>
      <ul>
        {properties.map(property => (
          <li key={property.id}>
            <Link to={`/property/${property.id}`}>
              {property.address} - Starting Price: {property.startingPrice} USDC
            </Link>
          </li>
        ))}
      </ul>
    </div>
  );
};

export default PropertyList;